<template>
  <div id="index">
    <Row>
      <Col span="12" class="header">
        <Card :bordered="false" :padding=0>
          <div class="title">
            <img src="../assets/logo2.png">
            <h1>{{sys.name}}</h1>
            <time>{{today}}</time>
          </div>
          <svg class="waves" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 24 150 28" preserveAspectRatio="none" shape-rendering="auto">
            <defs>
              <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z" />
            </defs>
            <g class="parallax">
              <use xlink:href="#gentle-wave" x="48" y="0" fill="rgba(255,255,255,0.7" />
              <use xlink:href="#gentle-wave" x="48" y="3" fill="rgba(255,255,255,0.5)" />
              <use xlink:href="#gentle-wave" x="48" y="5" fill="rgba(255,255,255,0.3)" />
              <use xlink:href="#gentle-wave" x="48" y="7" fill="#fff" />
            </g>
          </svg>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="countdown">
        <h1>倒计时</h1>
        <p>
          大獎池24小時開壹次獎，每有壹筆新投入，時間將按照1个以太延長3小時计算，獎池100%的獎金將給予最後300名投資額最大玩家。小獎池24小時開壹次獎，100%開出，壹等獎1名直接奪得奖池的1/3，二等獎10名平分奖池的1/3，三等獎20名平分奖池的1/3。</p>
        <div class="big">
          <img src="../assets/eth.png" alt="">
          <p>大奖池</p>
          <time>{{bigTime}}</time>
          <span>{{contractInfo.bigJackpot}} ETH</span>
        </div>
        <div class="small">
          <img src="../assets/eth.png" alt="">
          <p>小奖池</p>
          <time>{{smallTime}}</time>
          <span >{{contractInfo.smallJackpot}} ETH</span>
        </div>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="involvement">
        <Divider class="title">我要参与</Divider>
        <Card class="card" :padding=0 :bordered="false">
          <Tabs class="tabs">
            <TabPane label="参与">
              <div class="inPlan">
                <p>当前燃烧比例 <span>1ETH = {{contractInfo.consume}}TTS</span></p>
                <div class="btnGroup">
                  <ButtonGroup>
                    <Button ghost @click="inPlanAmount=1">1~10ETH</Button>
                    <Button ghost @click="inPlanAmount=11">11~30ETH</Button>
                    <Button ghost @click="inPlanAmount=31">31ETH</Button>
                  </ButtonGroup>
                </div>
                <Input class="input" v-model.number="inPlanAmount"><span slot="append">ETH</span></Input>
                <Button type="success" long @click="buyPlan">参与</Button>
              </div>
            </TabPane>
            <TabPane label="火种">
              <div class="ticket">
                <p>购买入场火种 <span>1ETH = {{contractInfo.rate}}TTS</span></p>
                <Input class="input" v-model.number="rateAmount"><span slot="append">ETH</span></Input>
                <Button type="success" long @click="buyRate">购买</Button>
              </div>
            </TabPane>
            <TabPane label="邀请">
              <div class="inTeam">
                <p>我的推薦人是: {{user&&user.sup?user.sup.addr:'--'}}</p>
                <p>我的直推數量: {{user&&user.sup?user.sub.length:'--'}}</p>
                <p>我的邀請鏈接: {{user.addr||'--'}}</p>
                <Button type="success" long @click="doCopy">复制我的邀请链接</Button>
              </div>
            </TabPane>
            <TabPane label="规则">
              <ul class="rule">
                <li>
                  <Icon type="ios-star"/>
                  壹個賬號在出局前僅接受壹筆投資，出局後可繼續投資
                </li>
                <li>
                  <Icon type="ios-star"/>
                  靜態分紅按照每天0.35%~2.5%上下浮動
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動靜出局，按照1-10為投資額3倍，11-30為投資額4倍，大於31為投資額5倍
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動態推薦26%按照:12%->8%->6%獲取動態獎勵，可即時提現
                </li>
                <li>
                  <Icon type="ios-star"/>
                  小獎池達到指定時間後，小獎池不再沉澱，直到開啟下壹輪
                </li>
                <li>
                  <Icon type="ios-star"/>
                  大獎池達到指定時間後，不再沉澱，投資額直接進入分紅收益
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動態如果未達到條件，沉澱至分紅池
                </li>
              </ul>
            </TabPane>
          </Tabs>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="logs">
        <Divider class="title">统计数据</Divider>
        <Card class="card infoList " :padding=0 :bordered="false">
          <p slot="title">我的收益</p>
          <p><span class="label">已結算分紅收益</span> <span class="extra">-- ETH</span></p>
          <p><span class="label">奖池收益</span> <span class="extra">{{user.jackpot |flex(4)}} ETH</span></p>
          <p><span class="label">下次分红收益时间差</span> <span class="extra">{{smallTime}}</span></p>
          <p><span class="label">动静出局还差</span> <span class="extra">{{user| outNum | flex(4)}} ETH</span></p>
          <p><span class="label">3倍未结算预计收益</span> <span class="extra">{{user.static3 | flex(4)}} ETH <Button type="success" size="small" @click="settle('static3',user.static3)">提现</Button></span></p>
          <p><span class="label">4倍未结算预计收益</span> <span class="extra">{{user.static4 | flex(4)}} ETH <Button type="success" size="small" @click="settle('static4',user.static4)">提现</Button></span></p>
          <p><span class="label">5倍未结算预计收益</span> <span class="extra">{{user.static5 | flex(4)}} ETH <Button type="success" size="small" @click="settle('static5',user.static5)">提现</Button></span></p>
          <p><span class="label">推介奖励</span> <span class="extra">{{user.dynamic | flex(4)}} ETH <Button type="success" size="small" @click="settle('dynamic',user.dynamic)">提现</Button></span></p>
          <p><span class="label">未提币数量</span> <span class="extra">{{user.daysAmount | flex(4)}} ETH </span></p>
          <p><Button type="success" long> 提 现 收 益</Button></p>
        </Card>
        <Card class="card infoList " :padding=0 :bordered="false">
          <p slot="title">统计</p>
          <p><span class="label">大奖池当前总量</span> <span class="extra">{{contractInfo.bigJackpot | flex(4)}} ETH </span></p>
          <p><span class="label">小奖池当前总量</span> <span class="extra">{{contractInfo.smallJackpot | flex(4)}} ETH </span></p>
          <p><span class="label">火种焚毁倍率</span> <span class="extra">{{contractInfo.consume}} TIMES </span></p>
          <p><span class="label">已焚毁火种</span> <span class="extra">{{contractInfo.totalConsume}} TTS </span></p>
          <p><span class="label">已投入的总资金</span> <span class="extra">{{user.invest | flex(4)}} ETH </span></p>
          <p><span class="label">总的推介收益</span> <span class="extra">{{user.aDynamic | flex(4)}} ETH </span></p>
          <p><span class="label">团队</span> <span class="extra">{{user.shareholder | flex(4)}} ETH <Button type="success" size="small" @click="settle('shareholder',user.shareholder)">提现</Button></span></p>
          <p><span class="label">全球节点</span> <span class="extra">{{user.angelAmount | flex(4)}} ETH <Button type="success" size="small" @click="('angelAmount',user.angelAmount)">提现</Button></span></p>
        </Card>
        <Card class="card jcList" :padding=0 :bordered="false">
          <p slot="title">历史记录</p>
          <p class="big">大奖池</p>
          <p class="small">小奖池</p>
          <CellGroup>
            <Cell v-for="item in jp" v-if="item.isStarted" :title="'Round'+item.num.toString()" :extra="item.amount+' ETH'"/>
          </CellGroup>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
    import mixins from '../mixins'
    import moment from 'moment';
    import Bn from 'bignumber.js'
    import * as api from '../api'
    import * as types from '../store/mutation-types'
    export default {
        name: "index",
        mixins: [mixins],
        data() {
            return {
                msg: [],
                bigTime: null,
                smallTime: null,
                today: moment().format('YYYY.MM.DD'),
                inPlanAmount: 0,
                rateAmount: 0,
            }
        },
        filters:{
          flex(e,r){
              if(e){
                  return e.toFixed(r)
              }else{
                  return e;
              }
          },
          outNum(e){
              let n = new Bn(e.aStatic3).plus(e.aStatic4).plus(e.aStatic5).toNumber();
              let c = [e.investGroup[0]*3,e.investGroup[1]*4,e.investGroup[2]];
              let s = new Bn(c[0]).plus(c[1]).plus(c[2]).toNumber();
              return s-n;
          },
          outPlan(e){
            let n = new Bn(e.aStatic3).plus(e.aStatic4).plus(e.aStatic5).toNumber();
            return n;
          }
        },
        computed: {
            url() {
                if (this.user) {
                    return `${this.sys.url}/?sup=${this.user.addr}`
                } else {
                    return `${this.sys.url}/`
                }
            }
        },
        mounted() {
            // 初始化
            this.countTime();
            let that = this;
            this.sockets.subscribe('reload', async data => {
                if(data.reload){
                    that.init();
                    let user = await api.getUser();
                    that.$store.commit(types.UPDATE_USER,  user.user);
                }
            });
        },
        methods: {
            // 初始化
            init() {
                let that = this;
                this.$store.dispatch('initService');
                this.countTime();
                setTimeout(function () {
                    that.$store.dispatch('newContract');
                }, 5000)
            },
            // 结算
            async settle(e,r){
                if(r>0){
                    this.$store.commit(types.LOADDING, true);
                    let a = await api.settle({type:e,amount:r});
                    if(a.success){
                        let user = await api.getUser();
                        this.$store.commit(types.UPDATE_USER,  user.user);
                        this.$store.commit(types.LOADDING, false);
                        this.$Message.success('结算成功！')
                    }
                }else{
                    this.$Message.error('结算失败！')
                }
            },
            // 复制邀请码
            doCopy() {
                if (!this.user||!this.user._id) return this.$Message.error('您还不是本站会员！');
                if (this.account) this.$copyText(this.url).then(e => this.$Message.success('复制成功！'))
            },

            // 倒计时
            countTime: function () {
                let bTime = new Date(this.sys.bigTime).getTime() - new Date().getTime();
                let sTime = new Date(this.sys.smallTime).getTime() - new Date().getTime();
                if (bTime >= 0) this.bigTime = Math.floor(bTime / 1000 / 60 / 60 % 24) + ':' + (Math.floor(bTime / 1000 / 60 % 60) > 9 ? Math.floor(bTime / 1000 / 60 % 60) : '0' + Math.floor(bTime / 1000 / 60 % 60)) + ":" + (Math.floor(bTime / 1000 % 60) > 9 ? Math.floor(bTime / 1000 % 60) : '0' + Math.floor(bTime / 1000 % 60));
                if (sTime >= 0) this.smallTime = Math.floor(sTime / 1000 / 60 / 60 % 24) + ':' + (Math.floor(sTime / 1000 / 60 % 60) > 9 ? Math.floor(sTime / 1000 / 60 % 60) : '0' + Math.floor(sTime / 1000 / 60 % 60)) + ":" + (Math.floor(sTime / 1000 % 60) > 9 ? Math.floor(sTime / 1000 % 60) : '0' + Math.floor(sTime / 1000 % 60));
                setTimeout(this.countTime, 1000);
            },
            // 购买门票
            buyRate() {
                if(!this.user||!this.user._id) return ;
                let that = this;
                if (!this.user) return that.$Message.error('您还不是本站会员！');
                if (isNaN(this.rateAmount) || this.rateAmount < 1 || this.rateAmount % 1 > 0) return this.$Message.error('输入错误！');
                let hashs = null;
                contract.methods.tickets().send({
                    from: this.account,
                    value: web3.utils.toWei(new BN(this.rateAmount), 'ether')
                })
                    .on('transactionHash', async hash => {
                        that.$store.commit(types.LOADDING, true);
                        if (hash) await api.saveHash({hash, type: 1, amount: that.rateAmount});
                        hashs = hash;
                    })
                    .on('confirmation', function (confirmationNumber, receipt) {
                    })
                    .on('receipt', async receipt => {
                        await api.confirmHash({
                            item: {hash: hashs, type: 1, amount: that.rateAmount},
                            status: receipt.status
                        });
                        that.$store.commit(types.LOADDING, false);
                        that.rateAmount = 0;
                        if (receipt.status) that.$Message.success('购买成功！');
                    })
                    .on('error', error => {
                        that.$store.commit(types.LOADDING, false);
                    })
            },
            // 参与计划
            buyPlan() {
                if(!this.user||!this.user._id) return ;
                let that = this;
                if (!this.user) return that.$Message.error('您还不是本站会员！');
                if (isNaN(this.inPlanAmount) || this.inPlanAmount < 1 || this.rateAmount % 1 > 0) return this.$Message.error('输入错误！');
                let hashs = null;
                contract.methods.ticketConsume().send({
                    from: this.account,
                    value: web3.utils.toWei(new BN(this.inPlanAmount), 'ether')
                })
                    .on('transactionHash', async hash => {
                        if (hash) await api.saveHash({hash, type: 2, amount: that.inPlanAmount});
                        hashs = hash;
                        that.$store.commit(types.LOADDING, true);
                    })
                    .on('confirmation', function (confirmationNumber, receipt) {
                    })
                    .on('receipt', async receipt => {
                        await api.confirmHash({
                            item: {hash: hashs, type: 2, amount: that.inPlanAmount},
                            status: receipt.status
                        });
                        that.$store.commit(types.LOADDING, false);
                        if (receipt.status) that.$Message.success('参与成功！');
                    })
                    .on('error', error => {
                        that.$store.commit(types.LOADDING, false);
                    })
            },
        }
    }
</script>

<style lang="less">
  #index {
    background: rgb(30, 25, 49);
    .title {
      color: #008c7c;
      padding: 1rem 3rem;
      margin: 0;
      span {
        font-size: 1.4rem;
      }
    }

    .header {
      position: relative;
      width: 100%;
      height: 100vh;
      .ivu-card{
        background: none;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .title {
        img{
          width: 65%;
        }
        text-align: center;
        color: #ffffff;

        h1, time {
          text-shadow: 3px 3px 4px #000000;
        }

        h1 {
          font-size: 3rem;
          font-weight: 400;
        }

        time {
          margin-top: 1rem;
          border-top: 1px solid #ffffff;
          border-bottom: 1px solid #ffffff;
          width: 16rem;
          display: inline-block;
          padding: .5rem 0;
          font-size: 1.2rem;
        }
      }

      img {
        margin: 0;
      }
    }

    .countdown {
      width: 100%;
      overflow: hidden;
      padding: 1rem;
      text-align: center;

      & > h1 {
        font-size: 2.4rem;
        text-align: center;
        margin: 2rem;
      }

      & > p {
        text-indent: 2rem;
        line-height: 1.5rem;
        font-size: .9rem;
        text-align: left;
      }

      img {
        left: 8%;
        z-index: 1;
        opacity: 1;
        top: 40%;
        height: 2.8rem;
      }

      .big {
        color: #c5085b;
        border: 1px solid #C5085B;
      }

      .small {
        color: #bcbc37;
        border: 1px solid #BCBC37;
        margin-bottom: 2rem;
      }

      & > div {
        position: relative;
        width: 80%;
        display: inline-block;
        padding: 10px;
        margin-top: 3rem;
        border-radius: 7px;

        time, span {
          position: relative;
          display: block;
          z-index: 2;
        }

        p {
          font-size: 1.2rem;
        }

        time {
          font-size: 2.4rem;
        }

        span {
          font-size: 2rem;
          line-height: 2rem;
        }
      }
    }

    .involvement {
      width: 100%;

      .card {
        width: 90%;
        margin: auto;
        padding: 0;
        box-shadow: 0 0 10px 3px #00D7BD;
        overflow: hidden;
        background: #2a2a2a;
      }

      .tabs {
        .ivu-tabs-nav-scroll {
          background: #000000;

          .ivu-tabs-nav {
            width: 100%;

            .ivu-tabs-ink-bar {
              background-color: #CA0000;
            }

            .ivu-tabs-tab {
              text-align: center;
              color: #ffffff;
            }

            .ivu-tabs-tab-active {
              color: #CA0000;
            }
          }
        }

        .ivu-tabs-content {
          .inPlan, .inTeam, .ticket, .rule {
            padding: 0 20px 20px;
            color: #ffffff;

            p {
              line-height: 2rem;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
              span {
                color: #ca0000;
              }
            }
          }

          .inPlan, .ticket {
            p {
              font-weight: 500;
              margin-bottom: 1rem;
              font-size: 1.1rem;

              span {
                font-size: 1.1rem;
              }
            }
          }

          .inPlan, .ticket {
            .btnGroup {
              text-align: center;
              margin-bottom: 1rem;

              .ivu-btn-group {
                border-radius: 4px;
                box-shadow: 0 0 4px 1px #00D7BD;
              }

              button {
                border: 1px solid #00D7BD;
                color: #00d7bd;
                text-shadow: 1px 1px 2px #00D7BD;
              }
            }

            .input {
              margin-bottom: 1rem;
              height: 2.6rem;
              line-height: 2.6rem;

              input {
                text-indent: 1rem;
                height: 2.6rem;
              }
            }
          }

          .rule {
            list-style: none;

            i {
              color: #ffc500;
            }
          }
        }
      }
    }

    .logs {
      width: 100%;
      .ivu-card-head{
        padding: 0;
        p{
          border: none !important;
          text-align: center;
          font-size: 1.1rem;
          color: #ffffff;
          line-height: 3rem;
          height: 3rem;
        }
      }
      .card {
        border-radius: 0;
        background: none;

        .ivu-cell + .ivu-cell {
          border-top: 1px solid #008c7c;
        }

        div {
          color: #008c7c;
        }

        .btns .ivu-cell-main {
          width: 100%;
        }
      }
      .infoList{

        p{
          padding: 0 1rem;
          line-height: 40px;
          .extra{
            float: right;
          }
        }
      }
      .jcList {
        .big {
          color: #c5085b;
        }

        .small {
          color: #f7b500;
        }
        p {
          text-align: center;
          line-height: 3rem;
        }
      }
    }

  }
  .waves {
    position:absolute;
    width: 100%;
    height:15vh;
    margin-bottom:-7px; /*Fix for safari gap*/
    min-height:100px;
    max-height:150px;
    bottom: 0;
  }

  .content {
    position:relative;
    height:20vh;
    text-align:center;
    background-color: white;
  }

  /* Animation */

  .parallax > use {
    animation: move-forever 25s cubic-bezier(.55,.5,.45,.5)     infinite;
  }
  .parallax > use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
  }
  .parallax > use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
  }
  .parallax > use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
  }
  .parallax > use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
  }
  @keyframes move-forever {
    0% {
      transform: translate3d(-90px,0,0);
    }
    100% {
      transform: translate3d(85px,0,0);
    }
  }
  /*Shrinking for mobile*/
  @media (max-width: 768px) {
    .waves {
      height:60px;
      min-height:60px;
    }
    .content {
      height:30vh;
    }
    h1 {
      font-size:24px;
    }
  }
</style>



